import uniSegmentedControl from "../../components/uni-segmented-control/uni-segmented-control.vue";
import validCode from "../../components/p-validCode/validCode.vue";
import tkiFloatKeyboard from '../../components/tki-float-keyboard/tki-float-keyboard.vue';
export default {
	components: {
		uniSegmentedControl,
		validCode,
		tkiFloatKeyboard
	},
	data() {
		return {
			imgHeight: "",
			current: 0,
			items: ['燃油汽车', '新能源汽车'],
			carType: "",
			vals:"",
			keyMode:'keyboard',
			keyType:0,
			plateNumber: "",
		}
	},
	onLoad(option) {
		this.carType = option.cartype;
	},
	methods: {
		// 汽车键盘
		key3(){
			this.keyMode = "car"
			this.keyTitle = "汽车键盘"
			this.showKey()
		},
		// 显示键盘
		showKey(){
			this.$refs.keyb._keyShow()
		},
		// 键盘退格
		keyDel(){
			let d = this.vals
			this.vals = d.substring(0,d.length-1)
		},
		// 键盘输入值
		keyVal(v){
			this.plateNumber = this.vals + v
		},
		// 显示键盘后的回调
		keyShow(h){
			console.log(h)
		},
		// 隐藏键盘后的回调
		keyHide(){
		
		},
		async load() {
			var that = this;
			uni.getSystemInfo({
				success: function(res) { // res - 各种参数
					that.imgHeight = res.windowHeight;
				}
			});
		},
		goBack: function() {
			uni.clearStorageSync('CarType');
			uni.navigateBack();
		},
		onClickItem(e) { //tab点击事件
			var that = this;
			if (that.current !== e.currentIndex) {
				that.current = e.currentIndex;
			}
			that.$refs.code.clear();
		},
		getCode(val) {
			this.plateNumber = val;
		},
		Confirm(e) {
			var that = this;
			const {
				plateNumber
			} = this;
			var plate = this.plateNumber;
			console.log(plate);
			if (this.current == 0) {
				if (plate.length == 7) {
					uni.setStorage({
						key: 'CarType',
						data: that.carType,
						success() {
							uni.navigateTo({
								url: '/pages/driver/taxiDriver',
							})
						}
					})
				} else {
					uni.showToast({
						title: '请输入车牌号',
						icon: "none"
					})

				}
			}
			if (this.current == 1) {
				if (plate.length == 8) {
					uni.setStorage({
						key: 'CarType',
						data: that.carType,
						success() {
							uni.navigateTo({
								url: '/pages/driver/taxiDriver',
							})
						}
					})
				} else {
					uni.showToast({
						title: '请输入车牌号',
						icon: "none"
					})

				}
			}
		},
	}
}
